import Vue from 'vue'
import Router from 'vue-router'

import Home from '@/views/Home'
import CarNumber from '@/views/excel/CarNumber'
import GroupHome from '@/views/excel/Home'
import Doctor from '@/views/doctor/index'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      component: Home,
      name: 'Home',
      meta: {
        title: '交流与合作',
        prefix: true
      }
    },
    {
      path: '/excel/group',
      name: 'GroupHome',
      component: GroupHome,
      meta: {
        title: '表格按列分组',
        prefix: true
      }
    },
    {
      path: '/excel/car/number',
      name: 'CarNumber',
      component: CarNumber,
      meta: {
        title: '表格提取车牌号',
        prefix: true
      }
    },
    {
      path: '/doctor/index',
      name: 'Doctor',
      component: Doctor,
      meta: {
        title: '一街二村卫生室',
        prefix: false
      }
    }
  ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    if (to.meta.prefix) {
      document.title = '攀岩志团队 - ' + to.meta.title
    } else {
      document.title = to.meta.title
    }
  }
  next()
})

// 获取原型对象上的push函数
const originalPush = Router.prototype.push
// 修改原型对象中的push方法
Router.prototype.push = function push (location) {
  return originalPush.call(this, location).catch(err => err)
}

export default router
